












export default {
	name: 'uni-nav-bar',
	props: {
		color: {
			type: String,
			default: '#000000'
		},
		backgroundColor: {
			type: String,
			default: '#FFFFFF'
		},
		shadow: {
			type: [String, Boolean],
			default: true
		},
		border: {
			type: [String, Boolean],
			default: true
		}
	},
	computed: {
		hasShadow() {
			return String(this.border) === 'true'
		},
		hasBorder() {
			return String(this.border) === 'true'
		}
	}
}
